import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  footer: {
    "& .footer-container": {
      width: "100%",
      margin: "0 auto",

      "& .content": {
        display: "flex",
        justifyContent: "space-between",
        flexDirection: "column",

        "& .about": {
          margin: "auto",
          maxWidth: "100%",
          width: "410px",

          "& .logo": {
            maxWidth: "199px",
          },

          "& .byline": {
            margin: "5px 0 16px",
            display: "none",
          },

          "& .clients": {
            display: "none",
          },

          "& .clients-mobile": {
            margin: "25px 0",
            textAlign: "center",
          },
        },

        "& .menus": {
          display: "flex",
          flexGrow: 1,
          justifyContent: "space-between",
          flexDirection: "column",

          "& .menu-block": {
            padding: "0 20px",
            marginBottom: "24px",

            "& > *": {
              margin: "auto",
              maxWidth: "410px",
            },

            "& .menu-title": {
              color: "#0B132A",
              fontWeight: "bold",
              fontSize: "18px",
              lineHeight: "30px",
              marginBottom: "20px",
            },

            "& .menu": {
              listStyle: "none",

              "& li": {
                "& a": {
                  fontSize: "16px",
                  lineHeight: "30px",
                  color: "#4F5665",
                  textDecoration: "none",
                },
              },
            },
          },
        },
      },
    },

    "& .upper": {
      borderTop: "1px solid #0000001a",
      padding: "25px 0 15px",
    },

    "& .lower": {
      borderTop: "1px solid #0000001a",
      padding: "18px 0",

      "& .footer-container": {
        "& .content": {
          flexDirection: "column",
          alignItems: "center",
          height: "100%",
          padding: "0 20px",
        },

        "& .copyright": {
          order: 1,
          margin: "22px 0 32px",

          "& p": {
            fontSize: "16px",
            lineHeight: "30px",
            color: "#AFB5C0",
          },
        },

        "& .links": {
          order: 2,
          display: "flex",
          justifyContent: "space-between",
          listStyle: "none",
          alignItems: "center",
          alignSelf: "center",
          flexDirection: "column",

          "& a": {
            fontSize: "16px",
            lineHeight: "30px",
            color: "#4F5665",
            textDecoration: "none",
          },
        },

        "& .social": {
          order: 0,
          display: "flex",
          justifyContent: "space-between",
          alignSelf: "center",

          "& .label": {
            fontSize: "16px",
            lineHeight: "30px",
            color: "#4F5665",
            marginRight: "75px",
          },

          "& .links": {
            flexDirection: "row",

            "& .link-item:not(:last-child)": {
              marginRight: "20px",
            },

            "& .link": {
              alignItems: "center",
              backgroundColor: "#ffffff",
              borderRadius: "50%",
              boxShadow: "0px 13px 18px rgba(17, 19, 35, 0.08)",
              color: "#12B0EE",
              display: "flex",
              height: "34px",
              justifyContent: "center",
              width: "34px",

              "& .fab": {
                fontSize: "22px",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {
    footer: {
      "& .footer-container": {
        maxWidth: "90%",
      },
    },
  },
  [theme.breakpoints.up("sm")]: {
    footer: {
      "& .footer-container": {
        width: "95%",
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    footer: {
      "& .footer-container": {
        width: "90%",
        maxWidth: "1100px",

        "& .content": {
          flexDirection: "row",

          "& .about": {
            "& .byline": {
              display: "block",
            },

            "& .clients": {
              display: "block",
            },

            "& .clients-mobile": {
              display: "none",
            },
          },

          "& .menus": {
            flexDirection: "row",
          },
        },
      },

      "& .upper": {
        padding: "50px 0 25px",
      },

      "& .lower": {
        borderTop: "none",
        backgroundColor: "#EFEFEF",
        padding: 0,

        "& .footer-container": {
          height: "56px",

          "& .content": {
            flexDirection: "row",
            padding: 0,
          },

          "& .copyright": {
            order: 0,
            margin: 0,

            "& p": {
              color: "#4F5665",
            },
          },

          "& .links": {
            order: 1,
            alignSelf: "center",
            flexDirection: "row",

            "& .link-item:not(:last-child)": {
              marginRight: "20px",
            },
          },

          "& .social": {
            order: 2,
            alignSelf: "center",

            "& .label": {
              marginRight: "30px",
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    footer: {
      "& .footer-container": {
        maxWidth: "80%",
      },
    },
  },
  [theme.breakpoints.up("xl")]: {
    footer: {
      "& .footer-container": {
        width: "70%",
      },
    },
  },
}));

export default useStyles;
