import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "terms-n-conditions": {
    backgroundColor: "transparent",
    margin: "0 auto",
    maxWidth: "90%",
    paddingTop: "56px",

    "& .content": {
      margin: "58px auto 0",
      maxWidth: "810px",

      "& .page-header": {
        marginBottom: "96px",

        "& h1": {
          color: "#000000",
          fontSize: "58px",
          fontWeight: 900,
          lineHeight: "64px",
          marginBottom: "24px",
          textAlign: "center",
        },

        "& p": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "24px",
          textAlign: "center",
        },
      },

      "& .data": {
        "& h4": {
          color: "#24272C",
          fontSize: "24px",
          fontWeight: 700,
          lineHeight: "24px",
          textAlign: "justify",
        },

        "& p,& h6": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "24px",
          textAlign: "justify",
        },

        "& > p, & > .block": {
          marginBottom: "24px",

          "&:last-child": {
            marginBottom: "45px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "terms-n-conditions": {
      maxWidth: "95%",
    },
  },
  [theme.breakpoints.up("md")]: {
    "terms-n-conditions": {
      maxWidth: "1100px",
      paddingTop: "67px",
      width: "90%",
    },
  },
  [theme.breakpoints.up("lg")]: {
    "terms-n-conditions": {
      maxWidth: "80%",
    },
  },
  [theme.breakpoints.up("xl")]: {
    "terms-n-conditions": {
      maxWidth: "70%",
    },
  },
}));

export default useStyles;
